.productsHeading{
    margin: 2vmax auto;
    width: 15vw;
    border-bottom: 1px solid rgba(0,0,0,0.171);
    padding: 2vmax;
    color: rgba(0, 0, 0, 0.678);
    font: bold 2vmax "Roboto";
    text-align: center;
}
.products{
    display: flex;
    flex-wrap: wrap;
    padding: 0.5vmax;
    justify-content: center;
    min-height: 35vh;
}
.paginationBox{
   display: flex;
   justify-content: center;
   margin: 6vmax;
}

/* inside pagination component , there is ul whose class is pagination and li whose class is page-items  */
.pagination{
    display: flex;
    justify-content: center;
    padding: 0;
}
.products-heading{
    display: flex;
 justify-content: center;
 margin: 75px;
 margin-bottom: 100px;
 color: #000066;
 font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 70px;

}
.page-item{
    background-color: rgb(255, 255, 255);
    list-style: none;
    border: 1px solid rgba(0, 0, 0, 0.178);
    padding: 1vmax 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;
}
.page-item:first-child{
    border-radius: 5px 0 0 5px;
}
.page-item:last-child{
    border-radius: 0 5px 5px 0;
}
.page-link{
    text-decoration: none;
    font: 300 1vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
}
.page-item:hover{
    background-color: rgb(230, 230, 230);
}
.page-item:hover .page-link {
    color: rgb(0, 0, 0);
}

.pageItemActive {
    background-color: #000066;
}

.pageLinkActive {
    color: white;
}
.filterBox {
    width: 10vmax;
    position: absolute;
    top: 10vmax;
    left: 4vmax;
}

.categoryBox {
    padding: 0%;
}

.category-link {
    list-style: none;
    color: rgba(0, 0, 0, 0.61);
    font: 400 0.8vmax "Roboto";
    margin: 0.4vmax;
    cursor: pointer;
    transition: all 0.5s;
}

.category-link:hover {
    color: #000066;
}

.filterBox>fieldset {
    border: 1px solid rgba(0, 0, 0, 0.329);
}

@media screen and (max-width: 600px) {
    .filterBox {
        width: 20vmax;
        position: static;
        margin: auto;
    }

    .page-link {
        font: 300 1.7vmax "Roboto";
    }

    .category-link {
        font: 400 1.8vmax "Roboto";
    }
}